*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 32pt;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.8);
  font-size: 14px;
}

.btn {
  display: block;
  font-size: 12px;
  border: 2px solid;
  border-radius: 25px;
}

.order_btn {
  padding: 10px 40px;
  color: #212864;
  border-color: #212864;
  transition: 0.2s;
}

.order_btn:hover {
  color: white;
  background-color: #212864;
}

.upper_head, .head {
  transition: 0.2s all;
  position: relative;
  display: flex;
  width: 70vw;
  gap: 10px;
  justify-content: space-between;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
}

.head {
  width: 95vw;
  margin: 0 auto;
  margin-top: 10px;
}

.logo {
  max-width: 200px;
  max-height: 100px;
}
.logo img {
  width: 100%;
  object-fit: contain;
}

.nav_mobile {
  display: none;
  background-color: #212864;
  border-radius: 10px;
  padding: 20px;
}

.logo p {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
}

.numbers,
.adress {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}

.phone_number {
  font-size: 16px;
  font-weight: bold;
  color: #212864;
}

.social {
  width: 182px;
  flex-shrink: 0;
}

.social_icons {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}

.nav {
  background-color: #15193D;
  height: 55px;
}

.nav_bar,
.nav_catalog {
  max-width: 88vw;
  margin: 0 auto;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.nav_link {
  color: white;
  font-size: 14px;
  transition: 0.1s;
}

.nav_link:hover,
.nav_link:active {
  color: #FFC239;
}

.catalog {
  background-color: #8C93D1;
  height: 55px;
}
.catalog .vertical {
  height: 45px;
  width: 2px;
  background-color: rgba(255, 255, 255, 0.4);
}

.sq {
  position: absolute;
  right: -10px;
  top: 50%;
  transform: translate(-50%, -50%) scale(0.5);
  margin-top: 0px;
  display: none;
}
.sq * {
  transition: 0.2s all;
}

.myLabel {
  cursor: pointer;
}

.line {
  width: 40px;
  height: 6px;
  background: #8C93D1;
  margin-bottom: 6px;
  border-radius: 10px;
  border-radius: 10px;
  background: #8C93D1;
}

#l1 {
  transform-origin: left;
}

input {
  display: none;
}

#myCheck:checked ~ #l1 {
  transform: rotate(45deg);
  margin-top: -12px;
  margin-left: 6px;
}

#myCheck:checked ~ #l3 {
  transform: rotate(135deg);
  margin-top: 8px;
}

#myCheck:checked ~ #l2 {
  display: none;
}

@media (max-width: 809px) {
  h1 {
    font-size: 28px;
  }
  .sq {
    display: block;
  }
  .upper_head, .head {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }
  .head {
    position: fixed;
    z-index: 2;
    margin: 0;
    width: 100vw;
    border-radius: 0;
  }
  .upper_head {
    position: fixed;
    top: 100px;
    right: -100%;
  }
  .upper_head.active {
    right: 5px;
    top: 100px;
    z-index: 2;
  }
  .catalog, .nav {
    display: none;
  }
  .nav_mobile {
    display: block;
  }
  .nav_bar {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  html, body {
    overflow-x: hidden;
  }
}
.face {
  position: relative;
  height: 600px;
  background-image: url(../img/head_background.jpg);
  background-color: rgba(0, 0, 0, 0.4);
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: multiply;
  background-position: 50% 50%;
}
.face .text {
  position: absolute;
  top: 10vh;
  left: 10vw;
}
.face .text h1 {
  max-width: 875px;
  color: white;
  font-size: 48px;
  font-weight: bold;
}
.face .text p {
  margin-top: 25px;
  font-family: "Montserrat";
  font-size: 16px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
  max-width: 470px;
}
.face .text .calculate_group {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}
.face .text .calculate_group a {
  color: white;
}
.face .text .calculate_group a #circle,
.face .text .calculate_group a #vector {
  transition: all 0, 1s ease;
}
.face .text .calculate_group a #Group_calculate {
  stroke: white;
}
.face .text .calculate_group a #Group_calculate:hover {
  stroke: #FFC239;
}
.face .text .calculate_group a:hover {
  color: #FFC239;
}

@media (max-width: 809px) {
  .face .text {
    top: 110px;
    left: 10px;
    max-width: 100vw;
  }
  .face .text h1 {
    font-size: 30px;
  }
  .face .text p {
    font-size: 14px;
  }
}
.tabs {
